import {
  request
} from "../../utils/request";

// 1.
// 
export function getTeacherAllPapers(params) {
  return request({
    method: 'GET',
    url: '/tpaper/getAllPapers',
    params
  })
}

// 
export function updatePaperInfo(data) {
  return request({
    method: 'POST',
    url: '/tpaper/updatePaperInfo',
    data
  })
}

